import React from 'react'
import { Form, Input, Modal, Switch, DatePicker, Cascader } from 'antd'
import {connect} from 'react-redux'

import "./index.less"
@connect(
  state=>state
)
class UserModal extends React.Component{
  constructor (props){
    super(props)
    this.state = {
      addressVisible: false,
      data:[
        // {
        //   key: '1',
        //   name: '小丸子',
        //   phone: '13456801341',
        //   address: '浙江省杭州市滨江区悦湾小区123',
        //   address_select:['zhejiang','hangzhou','xihu'],
        //   specific_address:'123',
        //   default:true
        // },
        // {
        //   key: '2',
        //   name: '小篮子',
        //   phone: '13456801341',
        //   address: '浙江省杭州市滨江区悦湾小区123',
        //     address_select:['zhejiang','hangzhou','xihu'],
        //     specific_address:'123',
        //     default:false
        // },
      ]
    }
  }

  handleok = (e) => {
    this.props.form.validateFieldsAndScroll((err, value) => {
      if(err) return
      // this.props.cartData(value)
      const {
        addressVisible,
        title,
        footerNull
      } = this.props

      this.props.onOk(false,value, title)

    })
  }

  handleCancel = (e) => {
    this.props.onCancel();
  }
  render(){
    // console.log(this.props)
    const FormItem = Form.Item;
    const { TextArea } = Input;
    const { getFieldDecorator } = this.props.form;
    const { addressVisible, title, footerNull, data = {} } = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 4},
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
      },
    };
    const options = [{
      value: '浙江省',
      label: '浙江省',
      children: [{
        value: '杭州市',
        label: '杭州市',
        children: [{
          value: '西湖',
          label: '西湖',
        }],
      }],
    }, {
      value: '江苏省',
      label: '江苏省',
      children: [{
        value: '南京市',
        label: '南京市',
        children: [{
          value: '宗华门',
          label: '宗华门',
        }],
      }],
    }];
    return(
      <div className="preview_browsing">
        <Modal
          title={title}
          footer={footerNull}
          className="address"
          visible={addressVisible}
          onOk={this.handleok}
          onCancel={this.handleCancel}
        >
          {title === '编辑信息'||title === '新建用户'?
            (
              <div className="modal_invoice">
                <h6>发票信息</h6>
                <Form className="form_shop form_content">
                  <FormItem label="用户姓名"  {...formItemLayout}>
                    {getFieldDecorator('name', {
                      rules: [{ required: true, message: '请输入发票抬头' }],
                    })(
                      <Input defaultValue={data.name} />
                    )}
                  </FormItem>
                  <FormItem label="用户性别"  {...formItemLayout}>
                    {getFieldDecorator('gender',{
                      rules: [{ required: true, message: '请输入发票内容' }],
                    })(
                      <Input defaultValue={data.gender} />
                    )}
                  </FormItem>
                  <FormItem label="用户年龄"  {...formItemLayout}>
                    {getFieldDecorator('age')(
                      <Input defaultValue={data.age} />
                    )}
                  </FormItem>
                  <FormItem label="省市区"  {...formItemLayout}>
                    {getFieldDecorator('address_select', {
                      rules: [{ required: true, message: '请选择省市区!' }],
                    })(
                      <Cascader options={options}  placeholder="请选择省市区" />

                    )}
                  </FormItem>
                  <FormItem label="详情地址"  {...formItemLayout}>
                    {getFieldDecorator('specific_address', {
                      rules: [{ required: true, message: '请输入详细地址!' }],
                    })(
                      <TextArea defaultValue={data.specific_address} placeholder="" autosize={{ minRows: 2, maxRows: 6 }} />
                    )}
                  </FormItem>
                </Form>
              </div>
            ):null
          }
          {
            title === "查看信息"?
              (<div className="modal_invoice">
                <h6>姓名: {data.name}</h6>
                <h6>年龄: {data.age}</h6>
                <h6>性别: {data.gender}</h6>
                <h6>地址: {data.address_select}</h6>
                <h6>详细地址: {data.specific_address}</h6>

              </div>):null
          }
        </Modal>
      </div>
    )
  }
}
export default Form.create()(UserModal)