import React from 'react'
import { Table, Button, Popconfirm, Icon, Switch, Form, Badge, Pagination } from 'antd'
import ItemModal from  '../../component/modal/item_modal'
import './index.less'
import {connect} from 'react-redux'
import { dataAdress } from '../../redux/address_redux'


const orderStatus = ['待发货','交易成功']
const invoiceStatus = ['待开票','已开票']
@connect(
  state=>state,
  {dataAdress}
)
class UserManage extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      itemVisible: false,
      editRow: {},
      modalType: "新建地址",
      footerNull:undefined,
      data:[
        {
          id:'1',
          key: '1',
          itemName:'商品1',
          itemPrice: 222.11,
          itemCount:20,
          itemDesc:'这是一个商品',
        },
      ]
    }
  }
  componentDidMount(){

  }
  //提交
  handleok = (visible,value, ) => {
    this.setState({
      itemVisible: visible,
      data: this.state.data.concat([value])
    })
  }

  //编辑用户弹窗
  modal = (type, record) => {
    console.log(type)
    if(type === '编辑商品') {
      this.props.dataAdress(record)
      console.log(record)
      this.setState({
        modalType: type,
        record: record,
        currentData: record,
        footerNull:undefined
      }, () => {
        this.setState({editRow: record})
      })
    }
    if(type === '查看详情'){
      this.setState({
        record:[],
        itemVisible: true,
        modalType: type,
        footerNull: null,
        currentData: record,
      })
    }
    else {
      this.setState({
        record:[],
        itemVisible: true,
        modalType: type,
        footerNull:undefined
      })
    }
  }
  //close modal
  onCancel = () => {
    this.setState({
      itemVisible:false
    })
  }
  //删除行
  onDelete = (record,index) => {
    console.log(index)
    const dataChange = this.state.data
    dataChange.splice(index, 1);
    this.setState({
      data:dataChange
    })
  }
  render() {
    const { itemVisible, modalType,footerNull}  = this.state
    const columns = [{
      title: '商品名称',
      dataIndex: 'itemName',
    },
      {
        title: '商品价格',
        dataIndex:'itemPrice',
      },
      {
        title: '商品库存',
        dataIndex: 'itemCount',
      },
      {
        title: '商品描述',
        dataIndex: 'itemDesc',
      },
      {
        title: '操作',
        dataIndex: 'delete',
        render: (value, record, index) => {
          return (
            <div>
              {/* <Button onClick={() => this.modal('编辑商品', record, index)}>编辑</Button> */}
              <Button style={{marginLeft: '15px'}} onClick={() => this.modal('查看详情', record, index)}>查看</Button>
              <Popconfirm
                title="确认要删除这行码"
                onConfirm={() => this.onDelete(record, index)}
              >
                <Button style={{marginLeft: '15px'}}>删除</Button>
              </Popconfirm>
            </div>
          )
        }
      },
    ];
    return (
      <div className="container_manager_address container_width">
        <div className="shop_top container_top">
          {/*<h2>{menuName}</h2>*/}
          <h2>商品管理</h2>
        </div>
        <div className="type_button">
          <Button
            type="primary"
            onClick={() => this.modal('新建商品')}
          >
            <Icon type='plus' />
            新建
          </Button>
        </div>
        <Table
          columns={columns}
          dataSource={this.state.data}
          onDelete={this.onDelete}
          rowKey={record => record.key}
          pagination={false}
        />
        <Pagination
          current={1} total={1} pageSize={1}
        />
        <ItemModal
          data={this.state.currentData}
          itemVisible={itemVisible}
          title={modalType}
          onOk={this.handleok}
          onCancel={this.onCancel}
          footerNull={footerNull}
        />
      </div>
    );
  }
}

export  default Form.create()(UserManage)