import  React from 'react';
import  axios from 'axios'
import {
  Tabs,
  Form,
  Row,
  Col,
  Icon,
  Input,
  Button,
  Checkbox,
  Cascader
} from 'antd';
import { withRouter } from "react-router-dom"
import LogReg from '../../component/background/log_reg';
import './login.less'

    const options = [{
      value: '浙江省',
      label: '浙江省',
      children: [{
        value: '杭州市',
        label: '杭州市',
        children: [{
          value: '西湖区',
          label: '西湖区',
        }],
      }],
    }, {
      value: '江苏省',
      label: '江苏省',
      children: [{
        value: '南京市',
        label: '南京市',
        children: [{
          value: '宗华门',
          label: '宗华门',
        }],
      }],
    }];
@withRouter
class LoginForm extends React.Component {

  state = {
      count:0,
      autoLogin:true,
  }
  //忘记密码
  ForgotPwd = () => {
    this.props.history.push('/forgotpwd')
  }
  //注册跳转
  register = () => {
    this.props.history.push('/register')
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log(values, '====>>>>>')
        const list = localStorage.getItem('address_list')
        const listArray = (JSON.parse(list) || []).map((v) => {
          return {
            ...v,
            default: false,
          }
        })
        listArray.push({
          id: listArray.length,
          key: listArray.length,
          name: '新用户',
          phone: '13456801341',
          address: '',
          addressSelect: values.addressSelect,
          specificAddress: '123',
          default: true,
        })
        if (values.userName === 'admin') {
          localStorage.setItem('authList', ['buyer', 'admin'])
        } else {
          localStorage.setItem('authList', ['buyer'])
        }
        localStorage.setItem('address_list', JSON.stringify(listArray))
        console.log('Received values of form: ', values);
        this.props.history.push('/home')
      }
    });
  }
  //记住密码
  changgeAutoLogin = e => {
    this.setState({
      autoLogin:e.target.checked
    })
    console.log(this.autoLogin)
  }
  onTabChange = type => {
    this.setState({type})
  }

  // componentDidMount(){
  //
  //   axios.get("/api/test/profile")
  //     .then(res=>{
  //       // if (res.status==200&&res.data.code===0) {
  //       // 	dispatch(authSuccess(res.data.data))
  //       // }else{
  //       // 	dispatch(errorMsg(res.data.msg))
  //       // }
  //       console.log(res);
  //
  //     })
  //
  //
  // }
  render() {
    const { getFieldDecorator } = this.props.form;
    const TabPane = Tabs.TabPane;
    const FormItem = Form.Item;
    return (
      <LogReg>
        <div className="container_login">
          <div className="content">
            <div className="logo-top">
              <img src={require('../../component/img/logo.jpg')} alt="" />
              <h1>商城</h1>
            </div>
            <Tabs
              defaultActiveKey={'account'}
            >
              <TabPane tab="登录" key="account" >
                <Form onSubmit={this.handleSubmit} className="login-form">
                  <FormItem>
                    {getFieldDecorator('userName', {
                      rules: [{ required: true, message: '请输入账号!' }],
                    })(
                      <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="请输入手机号" />
                    )}
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('password', {
                      rules: [
                        { required: true, message: '请输入密码!' },
                        {min:6, message:'密码长度不能小于6位'}
                        ],
                    })(
                      <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="请输入密码" />
                    )}
                  </FormItem>
                  
                  <FormItem >
                    {getFieldDecorator('addressSelect',{
                      rules: [{ required: true, message: '请选择省市区!' }],
                    })(
                      <Cascader options={options}  placeholder="请选择省市区" />

                    )}
                  </FormItem>
                  <FormItem>
                    {getFieldDecorator('remember', {
                      valuePropName: 'checked',
                      initialValue: true,
                    })(
                      <Checkbox >记住密码</Checkbox>
                    )}
                    <span className="login-form-forgot">
                    <a onClick={this.ForgotPwd}>忘记密码</a>
                  </span>
                  </FormItem>
                  <FormItem >
                    <Button type="primary" htmlType="submit" className="login-form-button btn_280">登录</Button>
                  </FormItem>
                </Form>
              </TabPane>
            </Tabs>
            {/* <a>第三方登录</a> */}
            <a onClick={this.register} className="register">注册</a>
          </div>
        </div>
      </LogReg>

    );
  }
}

const Login = Form.create()(LoginForm)
export  default  Login
