import React from 'react'
import { Form, Input, Modal, Switch, DatePicker, Cascader } from 'antd'
import {connect} from 'react-redux'

import "./index.less"
@connect(
  state=>state
)
class ItemModal extends React.Component{
  constructor (props){
    super(props)
    this.state = {
      itemVisible: false,
      data:[
        // {
        //   key: '1',
        //   name: '小丸子',
        //   phone: '13456801341',
        //   address: '浙江省杭州市滨江区悦湾小区123',
        //   address_select:['zhejiang','hangzhou','xihu'],
        //   specific_address:'123',
        //   default:true
        // },
        // {
        //   key: '2',
        //   name: '小篮子',
        //   phone: '13456801341',
        //   address: '浙江省杭州市滨江区悦湾小区123',
        //     address_select:['zhejiang','hangzhou','xihu'],
        //     specific_address:'123',
        //     default:false
        // },
      ]
    }
  }

  handleok = (e) => {
    this.props.form.validateFieldsAndScroll((err, value) => {
      if(err) return
      // this.props.cartData(value)
      this.props.onOk(false,value)

    })
  }

  handleCancel = (e) => {
    this.props.onCancel();
  }
  render(){
    // console.log(this.props)
    const FormItem = Form.Item;
    const { TextArea } = Input;
    const { getFieldDecorator } = this.props.form;
    const { itemVisible, title, footerNull, data = {} } = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 4},
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 16},
      },
    };
    const options = [{
      value: '浙江省',
      label: '浙江省',
      children: [{
        value: '杭州市',
        label: '杭州市',
        children: [{
          value: '西湖',
          label: '西湖',
        }],
      }],
    }, {
      value: '江苏省',
      label: '江苏省',
      children: [{
        value: '南京市',
        label: '南京市',
        children: [{
          value: '宗华门',
          label: '宗华门',
        }],
      }],
    }];
    return(
      <div className="preview_browsing">
        <Modal
          title={title}
          footer={footerNull}
          className="address"
          visible={itemVisible}
          onOk={this.handleok}
          onCancel={this.handleCancel}
        >
          {title === '编辑商品'||title === '新建商品'?
            (
              <div className="modal_invoice">
                <h6>商品信息</h6>
                <Form className="form_shop form_content">
                  <FormItem label="商品名称"  {...formItemLayout}>
                    {getFieldDecorator('itemName', {
                      rules: [{ required: true, message: '请输入发票抬头' }],
                    })(
                      <Input />
                    )}
                  </FormItem>
                  <FormItem label="商品价格"  {...formItemLayout}>
                    {getFieldDecorator('itemPrice',{
                      rules: [{ required: true, message: '请输入发票内容' }],
                    })(
                      <Input />
                    )}
                  </FormItem>
                  <FormItem label="商品描述"  {...formItemLayout}>
                    {getFieldDecorator('itemDesc')(
                      <Input />
                    )}
                  </FormItem>
                  <FormItem label="商品库存"  {...formItemLayout}>
                    {getFieldDecorator('itemCount')(
                      <Input />
                    )}
                  </FormItem>
                </Form>
              </div>
            ):null
          }
          {
            title === "查看商品"?
              (<div className="modal_invoice">
                <h6>商品名称: {data.itemName}</h6>
                <h6>商品价格: {data.itemPrice}</h6>
                <h6>商品描述: {data.itemDesc}</h6>
                <h6>商品库存: {data.itemCount}</h6>
              </div>):null
          }
        </Modal>
      </div>
    )
  }
}
export default Form.create()(ItemModal)