import React from 'react'
import { Table, Button, Popconfirm, Icon, Switch, Form, Badge, Pagination } from 'antd'
import UserModal from  '../../component/modal/user_modal'
import './index.less'
import {connect} from 'react-redux'
import { dataAdress } from '../../redux/address_redux'


const orderStatus = ['待发货','交易成功']
const invoiceStatus = ['待开票','已开票']
@connect(
  state=>state,
  {dataAdress}
)
class UserManage extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      loading: false,
      addressVisible: false,
      editRow: {},
      modalType: "新建地址",
      footerNull:undefined,
      data:[
        {
          id:'1',
          key: '1',
          name:'王睿智',
          age: '23',
          gender: '男',
          address_select: '江苏',
          specific_address: '平度市王家庄'
        },
      ]
    }
  }
  componentDidMount(){

  }
  //提交
  handleok = (val,value, title) => {
    this.setState({
      addressVisible: val,
      data: title === '新建用户' ? this.state.data.concat([value]) : this.state.data

    })
  }

  //编辑用户弹窗
  modal = (type, record) => {
    console.log(type)
    if(type === '编辑信息') {
      this.props.dataAdress(record)
      console.log(record)
      this.setState({
        modalType: type,
        currentData: record,
        record:record,
        footerNull:undefined
      }, () => {
             this.setState({
               addressVisible: true
             })
        // this.props.form.setFieldsValue({
        //   name: record.name,
        //   phone: record.phone,
        //   address_select: record.address_select,
        //   specific_address:record.specific_address,
        //   default:record.default
        // })
        this.setState({editRow: record})
        // console.log(this.state.editRow,record)
      })
    }
    if(type === '查看详情'){
      this.setState({
        record:[],
        modalType: type,
        currentData: record,
        footerNull:null
      }, () => {
                     this.setState({
                       addressVisible: true
                     })
      })
      // console.log(())
      // if (type === '新建地址') return;
    }
    // if(type === '新建地址'){
    //   this.setState({
    //     record:[],
    //     addressVisible: true,
    //     modalType: type,
    //   })
    //   if (type === '新建地址') return;
    // }
    else {
      this.setState({
        record:[],
        modalType: type,
        currentData: record,
        footerNull:undefined
      }, () => {
              this.setState({
                addressVisible: true
              })
      })
      // if (type === '新建地址') return;
    }
  }
  //close modal
  onCancel = () => {
    this.setState({
      addressVisible:false
    })
  }
  //删除行
  onDelete = (record,index) => {
    console.log(index)
    const dataChange = this.state.data
    dataChange.splice(index, 1);
    this.setState({
      data:dataChange
    })
  }
  render() {
    const { addressVisible, modalType,footerNull}  = this.state
    const columns = [{
      title: '姓名',
      dataIndex: 'name',
    },
      {
        title: '年龄',
        dataIndex:'age',
      },
      {
        title: '性别',
        dataIndex: 'gender',
      },
      {
        title: '地址',
        dataIndex: 'address_select',
      },
      {
        title: '详细地址',
        dataIndex: 'specific_address',
      },
      {
        title: '操作',
        dataIndex: 'delete',
        render: (value, record, index) => {
          return (
            <div>
              <Button onClick={() => this.modal('编辑信息', record, index)}>编辑</Button>
              <Button style={{marginLeft: '15px'}} onClick={() => this.modal('查看信息', record, index)}>查看</Button>
              {/*<Popconfirm*/}
                {/*title="确认要删除这行码"*/}
                {/*onConfirm={() => this.onDelete(record, index)}*/}
              {/*>*/}
                {/*<Button style={{marginLeft: '15px'}}>查看</Button>*/}
              {/*</Popconfirm>*/}
            </div>
          )
        }
      },
    ];
    return (
      <div className="container_manager_address container_width">
        <div className="shop_top container_top">
          {/*<h2>{menuName}</h2>*/}
          <h2>用户管理</h2>
        </div>
        <div className="type_button">
          <Button
            type="primary"
            onClick={() => this.modal('新建用户')}
          >
            <Icon type='plus' />
            新建
          </Button>
        </div>
        <Table
          columns={columns}
          dataSource={this.state.data}
          onDelete={this.onDelete}
          rowKey={record => record.key}
          pagination={false}
        />
        <Pagination
          current={1} total={1} pageSize={1}
        />
        {
          addressVisible ? <UserModal
            data={this.state.currentData}
            addressVisible={addressVisible}
            title={modalType}
            onOk={this.handleok}
            onCancel={this.onCancel}
            footerNull={footerNull}
          />: null
        }
        
      </div>
    );
  }
}

export  default Form.create()(UserManage)